import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';


@Component({
  selector: 'app-to-do-list',
  templateUrl: './to-do-list.component.html',
  styleUrls: ['./to-do-list.component.css'],
})
 
export class ToDoListComponent implements OnInit {
  public showclk:boolean = false; //for new button
  public buttonName:any = 'add';
  public editclk:boolean = false; // for Edit

  mytodos = [
    { mtodo:"Lunch with Lily", tododue: "Fri, Jan 18", todonotes:"Not any Notes", tododone: "Yes" },
    { mtodo:"Visit N. Hirano", tododue:"Sat, Jan 26", todonotes:"Conform the Meeting ahead of time", tododone: "No"}
  ];
  model:any={};
  model2: any={};
  msg: any="";

  addTodo(){                                        
    let now = moment(this.model.tododue).format('ddd, MMM DD');
    console.log(now);
    console.log(this.model)
    this.mytodos.push({mtodo:this.model.mtodo, 
                       tododue:now,
                       todonotes:this.model.todonotes,
                       tododone:this.model.tododone });
    this.model = {};
    this.msg = "Yes! New ToDo Add Success...";
    this.showclk= ! this.showclk;
    this.buttonName = "add"; 

  }
  deleteTodo(i){
    this.mytodos.splice(i,1);
    this.msg = "Hmmm! You've done one Task Successfully...";  
  }

  myValue;

  editTodo(k){
    this.editclk = ! this.editclk;
    this.model2.mtodo = this.mytodos[k].mtodo;
    this.model2.tododue = this.mytodos[k].tododue;
    this.model2.todonotes = this.mytodos[k].todonotes;
    this.model2.tododone = this.mytodos[k].tododone;
    this.myValue = k;
  }

  updateTodo(){
    let k = this.myValue;
    for(let i=0; i<this.mytodos.length; i++){
      if(i==k){
        this.mytodos[i] = this.model2;
        this.model2 = {};
        this.msg = "Yes! You've Update Your Todo Successfully...";
        this.editclk= ! this.editclk;
      }
    }
  }

  clickMe(){
    this.msg="";
  }
  
  constructor() { }

  ngOnInit() {
  }
toggle() {
  this.showclk = !this.showclk;
  if(this.showclk)
  this.buttonName = "remove";
  else
  this.buttonName = "add"; 
}
cancel(){
  this.editclk =!this.editclk;
}
canceln(){
  this.showclk =!this.showclk;
  this.buttonName = "add"; 
}
}
